
html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  font-size: 60px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding-top: 20px;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}
h1 {
  font-size: 30px;
  margin: 0 auto 20px;
}
h2 {
  font-size: 25px;
  margin: 0 120px 20px;
  text-align: left;
}
.clearfix:after {
  content: ' ';
  display:table;
  clear: both;
}



